#myVideo {
  position: fixed;
  min-width: 100%;
  min-height: 100%;
}

.content {
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #f1f1f1;
  width: 100%;
  height: 100%;
  padding: 5px;
}

.App {
  box-sizing: border-box;
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
  align-items: center;
}

.total {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.TotalItem--grey {
  color: grey;
}
.TotalItem--green {
  color: green;
}
.TotalItem--orange {
  color: orange;
}
.TotalItem--red {
  color: red;
}

.lastUpdated {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 25px;
  /* column-gap: 5px;
  row-gap: 0px; */
}

